import { css, excludeProps, modifier, styled } from '@lokalise/styled';
import type { InputHTMLAttributes } from 'react';

import { withInputContainerStyle, withInputFieldStyle, withInputSizeVariants } from '../../utils/styles';
import type { InputSizeVariants, InputStyleProps } from '../../utils/styles';
import { spacing, value } from '../../utils/theme';
import type { FlexProps } from '../Flex';
import { Flex } from '../Flex';

export const Container = styled.div`
	width: 100%;
	display: grid;
	gap: ${spacing(1)};
	grid-template-columns: minmax(50px, 1fr); /* This ensures Input doesn't overflow 100% */
`;

export const StyledInput = styled.input.withConfig({ shouldForwardProp: excludeProps(['error']) })<
	InputHTMLAttributes<HTMLInputElement> & InputStyleProps
>`
	${withInputFieldStyle};

	border: 0;
	min-width: 50px;
	width: 100%;
	padding: 0;
	display: block;
`;

export const InputIcon = styled.div`
	line-height: 1;
	font-size: 0;
`;

type InputContainerProps = {
	inputSize: InputSizeVariants;
	hasPrefix?: boolean;
	hasSuffix?: boolean;
	disabled?: boolean;
	error?: boolean;
};

export const InputContainer = styled(Flex).withConfig({
	shouldForwardProp: excludeProps(['inputSize', 'hasPrefix', 'hasSuffix', 'disabled', 'error']),
})<FlexProps & InputContainerProps>`
	${withInputContainerStyle};
	${withInputSizeVariants};

	align-items: center;
	padding: ${spacing(0, 3)};
	color: ${value('color.text.input.placeholder')};

	&:focus-within,
	[data-dev-state='focus'] & {
		color: ${value('color.border.active')};
	}

	// All items should preserve their width
	> * {
		flex-shrink: 0;
	}

	// Except input
	> ${StyledInput} {
		flex-shrink: 1;
	}

	${modifier(
		'hasPrefix',
		css`
			padding-left: ${spacing(2)};
		`,
	)};

	${modifier(
		'hasSuffix',
		css`
			padding-right: ${spacing(2)};
		`,
	)};
`;
